import React from 'react';
import ReactDOM from 'react-dom';
import Box from '@material-ui/core/Box';
import Divider from '@material-ui/core/Divider';
import TextField from '@material-ui/core/TextField';
import MySlider from './slider.js';
import Icon from '@material-ui/core/Icon';
import RadioButtonCheckedIcon from '@material-ui/icons/RadioButtonChecked';
import { CircleSlider } from 'react-circle-slider';

import './index.css';

class App extends React.Component {
	constructor(state) {
		super(state);
		this.state = {
			preset: 0,			
			deg: '',
			gradtype: '',
			bgcolor: [],
			bgcolorstr: '',
		};
	}

	componentDidMount() {
		this.setLayout();		
		if(localStorage.getItem('Preset')) {
			this.getNewStorage();
		} else {
			this.populateStorage();
		}		
		this.setBackgroundColor();
	}

	populateStorage = () => {
		localStorage.setItem('Preset', 0);
		let preset0 = {deg: 180, gradtype: false, bgcolor: [{color: '#FFFFFF', stop: 47}, { color: '#FE0000', stop: 54 }], bgcolorstr: 'linear-gradient(0deg, #FF0BF2 0%, #E7F62A 30%)'};
		let preset1 = {deg: 15, gradtype: true, bgcolor: [{color: '#FF0BF2', stop: 0}, { color: '#E7F62A', stop: 40 }, { color: '#5BE219', stop: 100 }], bgcolorstr: 'radial-gradient(circle, #FF0BF2 0%, #E7F62A 40%, #5BE219 100%)'};
		let preset2 = {deg: 90, gradtype: false, bgcolor: [{color: '#4845CB', stop: 30}, { color: '#58C937', stop: 60 }], bgcolorstr: 'linear-gradient(90deg, #4845CB 30%, #58C937 60%)'};
		let allPresets = [ preset0, preset1, preset2 ];
		localStorage.setItem('Presets', JSON.stringify(allPresets));
		this.getNewStorage();
	}

	getNewStorage = () => {
		let storedPreset = localStorage.getItem('Preset');
		this.setState({ preset: Number(storedPreset) });
		let receivedStorage = localStorage.getItem('Presets');
		let allStoredPresets = JSON.parse(receivedStorage);
		let activPreset = allStoredPresets[Number(storedPreset)];
		this.setState({ deg: activPreset.deg });
		this.setState({ gradtype: activPreset.gradtype });
		this.setState({ bgcolor: activPreset.bgcolor });
		document.getElementById('color0').style.background = allStoredPresets[0].bgcolorstr;
		document.getElementById('color1').style.background = allStoredPresets[1].bgcolorstr;
		document.getElementById('color2').style.background = allStoredPresets[2].bgcolorstr;
	}

	componentDidUpdate() {
		this.updatePresetStorage();
		this.updatePresetsStorage();
		this.setBackgroundColor();
		this.toggleDegSetter();
		this.setActiveBtn();
	}

	setActiveBtn = () => {
		document.getElementById('color0').style.border = '2px solid #D3C6C6';
		document.getElementById('color1').style.border = '2px solid #D3C6C6';
		document.getElementById('color2').style.border = '2px solid #D3C6C6';
		document.getElementById('color' + this.state.preset).style.border = '3px solid #FBC50B';		
	}

	updatePresetStorage = () => {
		localStorage.setItem('Preset', this.state.preset);
	}	

	updatePresetsStorage = () => {
		let receivedStorage1 = localStorage.getItem('Presets');
		let allStoredPresets1 = JSON.parse(receivedStorage1);
		allStoredPresets1[this.state.preset].deg = this.state.deg;
		allStoredPresets1[this.state.preset].gradtype = this.state.gradtype;
		allStoredPresets1[this.state.preset].bgcolor = this.state.bgcolor;
		let bgcolorstr =  this.createBgcolorStr();		
		allStoredPresets1[this.state.preset].bgcolorstr = bgcolorstr;
		localStorage.setItem('Presets', JSON.stringify(allStoredPresets1));
	}

	setBackgroundColor = () => {
		let bgcolorstr =  this.createBgcolorStr();
		document.getElementById('colorstring').innerText = bgcolorstr + ';';
		document.body.style.background = bgcolorstr;			
		document.body.style.backgroundRepeat = 'no-repeat';
		document.body.style.backgroundAttachment = 'fixed';	
		document.getElementById('color' + this.state.preset).style.background = bgcolorstr;
	}

	createBgcolorStr = () => {
		let bgcolorstr = '';
		if (this.state.gradtype) {
			bgcolorstr = `radial-gradient(circle`;			
		} else {
			bgcolorstr = `linear-gradient(` + this.state.deg + `deg`;		
		}		
		for (let i=0; i<this.state.bgcolor.length; i++) {
			bgcolorstr += ', ' + this.state.bgcolor[i].color + ' ' + this.state.bgcolor[i].stop + '%' 
		}
		bgcolorstr += ')';
		return bgcolorstr;
	}

	setLayout = () => {
		if (window.matchMedia("(max-width: 420px)").matches) {
		  /* The viewport is less than, or equal to, 420 pixels wide */		  
		  document.getElementById('layout').style.flexDirection = 'column';
		  document.getElementById('presets').style.flexFlow = 'column wrap';
		  document.getElementById('presets').style.justifyContent = 'center';
		  document.getElementById('presets').style.alignContent = 'space-evenly';
		  document.getElementById('colorCardList').style.maxWidth = '50vw';
		  document.getElementById('colorCardList').style.justifyContent = 'center';
		  document.getElementById('degSetter').style.flexDirection = 'column';
		  document.getElementById('degSetter').style.alignContent = 'center';
		  document.getElementById('addBtn').style.flexDirection = 'column';
		  document.body.style.fontSize = '12px'
		} else {
		  /* The viewport is greater than 420 pixels wide */		  
		}
	}

	toggleDegSetter = () => {
		if (this.state.gradtype) {
			document.getElementById('degSetter').style.display = 'none';
		} else {
			document.getElementById('degSetter').style.display = 'flex';
		}
	}

	setToRadial = () => {
		this.setState({ gradtype: true });
	}

	setToLinear = () => {
		this.setState({ gradtype: false });
	}

	addNewColorCard = () => {
		let bgcolor = this.state.bgcolor.slice();
		bgcolor.push({ color: '#F8F7F7', stop: '80' });
		this.setState({ bgcolor: bgcolor })
	}

	onColorChange = (event) => {
		const bgcolor = this.state.bgcolor.slice();
		bgcolor[event.target.name].color = event.target.value.toUpperCase();
		this.setState({ bgcolor: bgcolor })	
	}

	onRemoveCard = (event) => {		
		const bgcolor = this.state.bgcolor.slice();
		bgcolor.splice(event.target.parentElement.parentElement.firstChild.firstChild.name, 1);
		this.setState({ bgcolor: bgcolor })
	}

	onDegChange = (event) => {
		this.setState({ deg: event })
	}

	onDegChange1 = (event) => {
		this.setState({ deg: event.target.value })
	}

	onHover = () => {
		document.getElementById('controlbar').style.opacity = '1.0'
	}

	onHoverOut = () => {
		document.getElementById('controlbar').style.opacity = '0.3'
	}

	onStopPointChange = name => (e, value) => {
		const bgcolor = this.state.bgcolor.slice();
		bgcolor[name].stop = value;
		this.setState({ bgcolor: bgcolor })
	};

	onPresetChange = number => () => {
		this.setState({ preset: number });
		let receivedStorage = localStorage.getItem('Presets');
		let allStoredPresets = JSON.parse(receivedStorage);
		let activPreset = allStoredPresets[number];
		this.setState({ deg: activPreset.deg });
		this.setState({ gradtype: activPreset.gradtype });
		this.setState({ bgcolor: activPreset.bgcolor });
	};

	render() {	
		return(
			<Box style={{ minHeight: '100vh' }} display="flex" flexDirection="column" alignItems='center' justifyContent="space-evenly">
				<h1>Background Generator</h1>
				<div id='colorstring' style={{maxWidth: '70vw'}}></div>
				<Box display='flex' flexDirection='column' className='background' id='controlbar' onMouseOver={this.onHover} onMouseLeave={this.onHoverOut} >
					<Box display="flex" flexDirection="row" justifyContent="center" id='layout'>
						<Controll gradTypeR={this.setToRadial} gradTypeL={this.setToLinear} />
						<Box display='flex' flexDirection='column' justifyContent="space-evenly" className='degSetter background' id='degSetter'>
							<Box display='flex' flexDirection='column' justifyContent="center" alignItems='center'>
								<CircleSlider 
									onChange={this.onDegChange} 
									value={this.state.deg} 
									size={80}
									max='360' 
									shadow={false}
									knobRadius={6}
									knobColor="#ff5722" 
									showTooltip={true}
									tooltipSize={25} 
									progressColor="#FDB11B"
									progressWidth={10}
									circleWidth={10}
								/>
							</Box>
							<Box display='flex' flexDirection='column' justifyContent="center" alignItems='center'>
								<TextField
						          id="outlined-number"
						          value={this.state.deg}
						          onChange={this.onDegChange1} 
						          style={{ width: 73, margin: 5 }}
						          size='small'
						          type="number"
						          variant="outlined"
						        />
					        </Box>
						</Box>
						<ColorCardList 
							bgcolor={this.state.bgcolor} 
							colorChange={this.onColorChange} 
							stopPointChange={this.onStopPointChange} 
							removeCard={this.onRemoveCard}
						 />
						<Box display='flex' className='background' id='addBtn'>
							<button type='button' onClick={this.addNewColorCard} className='addColor'>+</button>					
						</Box>					
					</Box>
					<Box>
						<Divider variant="middle" />
					</Box>
					<Box display='flex' flexDirection='row' justifyContent="space-evenly" id='presets'>
						<div className='colorBox' id='color0' onClick={this.onPresetChange(0)} />
						<div className='colorBox' id='color1' onClick={this.onPresetChange(1)} />
						<div className='colorBox' id='color2' onClick={this.onPresetChange(2)} />
					</Box>
				</Box>
			</Box>
		);
	}
}

class ColorCardList extends React.Component {
	render() {
		return (
			<Box display="flex" flexDirection="row" flexWrap='wrap' id='colorCardList' style={{ maxWidth: '64vw'}}>
				{
					this.props.bgcolor.map((color, i) => {
						return (
							<ColorCard 
								key={i}
								num={i}
								color={this.props.bgcolor[i].color} 
								stop={this.props.bgcolor[i].stop}
								colorChange={this.props.colorChange} 
								stopPointChange={this.props.stopPointChange}
								removeCard={this.props.removeCard}
							/>
						);
					})
				}
			</Box>
		);
	}
}

class ColorCard extends React.Component {
	render() {
		return(
			<Box className='background' display="flex" flexDirection="column" alignItems="center" justifyContent="space-evenly">
				<Box display="flex" flexDirection="row" justifyContent='space-between' style={{width: '100%'}}>
					<input 
						className="color" 					
						type="color"
						name={this.props.num}
						value={this.props.color} 
						onChange={this.props.colorChange} 
					/>					
					<button type='button' onClick={this.props.removeCard} className='closeColor'>X</button>				
				</Box>
				 <MySlider
				 	stop={this.props.stop}
				 	stopPointChange={this.props.stopPointChange(this.props.num)}
				 />
			</Box>
		);
	}
}

class Controll extends React.Component {
	render() {
		return(
			<Box className='background' display="flex" flexDirection="column" justifyContent="space-evenly">
				<button type='button' onClick={this.props.gradTypeL}><Icon fontSize="small" >texture</Icon><h4>Linear</h4></button>
				<button type='button' onClick={this.props.gradTypeR}><RadioButtonCheckedIcon fontSize="small"/><h4>Radial</h4></button>
			</Box>
		);
	}
}


ReactDOM.render(<App />, document.getElementById('root'));
